.song-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 60vh;

    img {
        width: 20%;
        border-radius: 50%;
    }
    h2 {
      text-transform: capitalize;
      font-size: 2.5rem;
      padding: 3rem 1rem 1rem 1rem;
    }
    h3 {
      font-size: 1.5rem;
    }
    
}
@media screen and (max-width: 768px) {
  .song-container {
    img {
      width: 60%;
    }
  }
}